.wish--wrapper {
  display: flex;
  flex-wrap: wrap;
}

.wish__item {
  padding: 8px;
  background-color: grey;
  cursor: pointer;
  margin: 2px 4px 4px 4px;
  cursor: pointer;
  border-radius: 12px;
}

.wish__item > span {
  padding-left: 4px;
  cursor: pointer;
  margin-left: 4px;
}

.wish__selected {
  background: #2e8b57;
  padding-right: 19px;
  color: #fff;
}

.wish__selected > .wish--remove {
  display: none;
}
