.input-box {
  width: 80%;
  padding: 5px;
  font-size: 30px;
  margin-bottom: 8px;
}

.no-data-msg {
  color: red;
}

.container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.suggestion-wrapper {
  position: absolute;
  top: 60px;
  width: 80%;
}

.suggestion-wrapper:focus + .suggestion-item {
  display: block;
}

.suggestion-item {
  padding: 8px;
  background-color: #ccc;
  cursor: pointer;
}

.suggestion-item:hover {
  background-color: grey;
}

.suggestion-item:hover > .label {
  color: #b8b8b8;
}

.label {
  font-size: 16px;
  color: #1f1f1f;
}

.sub-label {
  color: #292929;
  font-size: 10px;
}
