.card {
  height: 200px;
  width: 200px;
  background: #ffffff;
  margin: 50px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  justify-content: space-between;
  padding: 8px;
}

.card > img {
  height: 50px;
  width: 50px;
}

.card > .info > .label {
  font-size: 10px;
}

.info-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.card.descreption {
  width: 100%;
  height: auto;
  min-height: 200px;
  min-width: 200px;
}

@media only screen and (max-width: 600px) {
  .card {
    margin: 5px;
    width: 150px;
  }

  .card.descreption {
  }
}
